.title {
	margin: 0;
	padding: 0;
	@media(max-width: @phone) {
		font-size: 32px;
	}
}
.block-title {
	position: relative;
	width: 100%;
	padding: 15px;
	.ff-300();
	font-size: @font-36;
	color: @c-grey;
	&--white {
		color: @c-w;
	}
	&--white&::after {
		color: @c-w;
		background-color: @c-w;
	}
	&::after {
		content: '';
		position: absolute;
		width: 377px;
		height: 1px;
		background-color: @c-blue;
		left: 49%;
		bottom: 0;
		transform: translateX(-50%);
	}
}