.portfolio {
	display: flex;
	justify-content: center;
	padding-top: 30px;
	padding-bottom: 55px;
	background-color: @c-w;
}
.filter {
	position: relative;
	width: 100%;
	margin-top: 24px;
	display: flex;
	justify-content: center;
	&__mobile {
		position: relative;
		z-index: 3;
    background-color: #ffffff;
    border-radius: 25px;
	}
}
.list {
	display: flex;
	&-filter {
		display: flex;
		align-items: center;
		justify-content: space-evenly;
		max-width: 825px;
		color: @c-blue;
		border: 1px solid @c-blue;
		border-radius: 50px;
		overflow: hidden;
		@media(max-width: @tablet-p) {
			display: none;
		}
		&--mobile {
			display: none;
			position: absolute;
			max-width: 154px;
			bottom: -170px;
			background-color: #ffffff;
			padding-top: 30px;
			z-index: 1;
			border-radius: 15px;
			border: 1px solid @c-blue;
		}
	}
	&-filter__link {
		position: relative;
		display: block;
		padding: 14px 35px;
		.ff-300();
		font-size: @font-14;
		color: @c-blue;
		&:hover {
			transform: scale(1.02);
			color: #ffffff;
			background-color: @c-blue;
		}
		@media(max-width: @tablet) {
			padding: 14px 30px;
		}
		@media(max-width: @tablet-p) {
			padding: 7px 20px;
		}
		&--mobile {
			position: relative;
			display: none;
			padding: 14px 35px;
			.ff-300();
			font-size: @font-14;
			color: @c-blue;
			border: 1px solid @c-blue;
			border-radius: 50px;
			background-color: #ffffff;
			z-index: 2;
			@media(max-width: @tablet-p) {
				display: block;
			}
			&:hover {
				color: #ffffff;
				background-color: @c-blue;
			}
		}
	}
	&-filter__link::after {
		content:'';
		position: absolute;
		width: 1px;
		height: 38px;
		background-color: @c-blue;
		top:5px;
		right: 0;
		@media(max-width: @tablet-p) {
			display: none;
		}
	}
}
.filter__mobile:hover>.list-filter--mobile {
	@media(max-width: @tablet-p) {
		display: block;
	}
}
.list__item:last-child>.list-filter__link::after {
	background-color: transparent;
}
.portfolio-img {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-right: -20px;
	&__item {
		margin-top: 20px;
		margin-right: 20px;
	}
}