.card {
	display: flex;
	margin-top: 20px;
	.card-text {
		margin-right: 30px;
		@media(max-width: @tablet) {
			margin-right: 0;
		}
	}
	@media(max-width: @tablet) {
		flex-wrap: wrap;
		justify-content: center;
	}
	&--rewers {
		flex-direction: row-reverse;
		.card-text {
			margin-left: 30px;
			margin-right: 0;
			@media(max-width: @tablet) {
				margin-left: 0;
			}
		}
	}
	&-picture {
		@media(max-width: @tablet) {
			order:1;
		}	
	}
	&-text {
		@media(max-width: @tablet) {
			order:2;
		}
	}
}