* {
	box-sizing: border-box;
}
html {
	min-width: 320px;
	font-size: 16px;
}
body {
	display: flex;
	justify-content: center;
	.ff-400();
	font-size: 16px;
	width: 100%;
	min-width: 320px;
	min-height: 100vh;
	margin: 0;
	line-height: 1.4;
}
.index-body {
	position: relative;
	//background: #ffffff url("@{bg-img}index-bg.jpg") no-repeat center top;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		width: 100%;
		max-width: 1920px;
		height: 100vh;
		//max-height: 700px;
		background: #ffffff url("@{bg-img}index-bg.jpg") no-repeat center top;
		background-size: cover;
		z-index: -1;
	}
}
h1, h2, h3, h4, h5, h6, ul, p {
	padding: 0;
	margin: 0;
	font-weight: normal;
}
img {
	width: 100%;
	height: auto;
} 
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
a {
	display: inline-block;
}
figure {
	margin: 0;
	padding: 0;
}
.wrapper {
	display: flex;
	flex-direction: column;
	max-width: 1920px;
	width: 100%;
	min-height: 100vh;
	overflow: hidden;
}
.maincontent {
	flex: 1;
}
.layout-position {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	width: 100%;
	max-width: 1170px;
	padding-left: 15px;
	padding-right: 15px;
}
.img-responsive {
	max-width: 500px;
	width: 100%;
	height: auto;
}
.container-fl {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
input {
	border: none;
}
.visually-hidden:not(:focuc):not(:active),
input[type="checkbox"].visully-hidden,
input[type="radio"].visully-hidden {
	display: none;
	position: absolute;
	visibility: hidden;
	width: 1px;
	height: 1px;
	margin: -1px;
	bottom: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}
/* Для Chrome */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
   -webkit-appearance: none;
   -moz-appearance: none;
   appearance: none;
   margin: 0;
}
.visual-not {
	display: none;
}
input[type="number"]{
  -moz-appearance: textfield;
}