.index-blog {
	display: flex;
	justify-content: center;
	padding-top: 20px;
	padding-bottom: 60px;
	background-color: @c-bluel;
}
.blog-list {
	display: flex;
	justify-content: space-between;
	width: 100%;
	margin-top: 40px;
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 48%;
		&:nth-child(1n) {
			margin-right: 2%;
		}
		@media(max-width: @phone) {
			width: 100%;
			margin-right: 0;
			margin-top: 10px;
		}
	}
	&__item-link {
		margin-top: 15px;
		max-width: 515px;
		.ff-300();
		font-size: @font-18;
		color: @c-blue;
		text-transform: none;
	}
	&__item-link:hover {
		text-decoration: underline;
		color: darken(@c-blue, 10%);
	}
}