.cooperation {
	display: flex;
	justify-content: center;
	padding-top: 10px;
	padding-bottom: 65px;
	background-color: @c-w;
	@media(max-width: @tablet-p) {
		padding-bottom: 30px;
	}
}
.cooperation-list {
	margin-top: 45px;
	&__item {
		display: flex;
		width: 100%;
		//max-height: 260px;
		@media(max-width: @tablet-p) {
			flex-wrap: wrap;
		}
		img {
			max-height: 260px;
		}
	}
	&__item-img {
		width: 50%;
		@media(max-width: @tablet-p) {
			width: 100%;
		}
	}
}
.list-rewerse {
	flex-direction: row-reverse;
}
.cooperation-step {
	display: block;
	width: 50%;
	text-align: center;
	align-self: center;
	.ff-300();
	font-size: @font-32;
	@media(max-width: @tablet-p) {
		width: 100%;
		padding-top: 15px;
		padding-bottom: 15px;
		font-size: 28px;
	}
}