.page-header {
	position: relative;
	display: flex;
	justify-content: center;
	width: 100%;
	height: 10vh;
	padding-top: 35px;
	padding-bottom: 5px;
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		@media(max-width: 450px) {
			flex-wrap: wrap;
		}
	}
	&__phone {
		@media(max-width: 450px) {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-top: 10px;
		}
	}
	&__logo {
		@media(max-width: 450px) {
			width: 100%;
			display: flex;
			justify-content: center;
		}
	}
	@media(max-width: @tablet-p) {
		padding-top: 10px;
	}
}
.logo {
	display: flex;
	align-items:center;
	width: 165px;
	height: 60px;
	&__number {
		width: 60px;
		height: 60px;
		font-size: @font-36;
		border-radius: 50%;
	}
	&__text {
		.ff-900();
		margin-left: 10px;
		font-size: @font-28;
		color: #ffffff;
	}
}
.phone {
	max-width: 215px;
	height: 37px;
	padding-left: 35px;
	color: #ffffff;
	background: url("@{bg-img}icon-phone.png") no-repeat left top;
	&__text {
		font-size: @font-12;
		.ff-300();
	}
	&__link {
		.ff-500();
		font-size: @font-20;
	}
	&__link:hover {
		text-shadow: 1px 1px 2px black, 0 0 1em #534b41;
	}
}