.feedback {
	display: flex;
	justify-content: center;
	//min-height: 530px;
	padding-top: 40px;
	padding-bottom: 55px;
	background: url('@{bg-img}feedback-bg.jpg') no-repeat top center;
	background-size: cover;
	@media(max-width: @desktop) {
		padding-top: 0;
		padding-bottom: 10px;
	}
	&__discr {
		margin-top: 15px;
		.ff-300();
		font-size: @font-26;
		color: @c-w;
	}
	&__btn {
		margin-top: 20px;
		padding: 15px 40px;

	}
}
.container {
	display: flex;
	width: 100%;
	margin-top: 45px;
	@media(max-width: @desktop) {
		justify-content: space-around;
		margin-top: 10px;
	}
	@media(max-width: @tablet-p) {
		flex-wrap: wrap;
	}
}
.feedback-form {
	width: 66%;
	display: flex;
	margin-right: 30px;
	flex-direction: column;
	align-items: center;
	border-radius: 10px;
	@media(max-width: @tablet-p) {
		width: 100%;
		margin-right: 0;
	}
	input {
		width: 32%;
		padding: 15px;
		border-radius: 10px;
		outline: none;
	}
}
.feedback-send {
	width: 100%;
	max-height: 125px;
	margin-top: 15px;
	padding: 20px;
	border-radius: 10px;
	resize: none;
	outline: none;
}
.feedback-input__group {
	display: flex;
	justify-content: space-between;
	width: 100%;
	margin: 0;
	padding: 0;
	/* @media(max-width: @desktop) {
		flex-direction: column;
	} */
}
.contacts {
	@media(max-width: @tablet-p) {
		width: 100%;
	}
	
}
.contacts-list {
	display: flex;
	flex-direction:column;
	align-items: flex-start;
	@media(max-width: @tablet-p) {
		flex-direction:row;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}
	@media(max-width: @tablet-p) {
		justify-content: space-around;
	}
	&__item {
		margin-top: 15px;
	}
	&__item:first-child {
		margin-top: 0;
		@media(max-width: @desktop) {
			margin-top: 15px;
		}
	}
	&__link {
		position: relative;
		padding-left: 40px;
		padding-top: 3px;
		padding-bottom: 3px;
		.ff-500();
		font-size: @font-18;
		@media(max-width: @tablet-p) {
			min-width: 210px;
		}
	}
	&__link::after,
	&__link::before {
		content: "";
		position: absolute;
	}
	&__link::after {
		width: 27px;
		height: 27px;
		top: 0;
		left: 0;
		border: 1px solid @c-w;
		border-radius: 3px;
	}
	&__link:hover::after,
	&__link:focus::after {
		border: 1px solid @c-w;
		transform: scale(1.05);
	}
	&__link::before {
		width: 14px;
		height: 14px;
		top: 8px;
		left: 8px;
		background-color: #fff;
	}
	&__link-phone::before {
		background: url("@{bg-img}icon-cont-phone.png") no-repeat center center;
	}
	&__link-whatsapp::before {
		background: url("@{bg-img}icon-wa.png") no-repeat center center;
	}
	&__link-e-mail::before {
		background: url("@{bg-img}icon-cont-email.png") no-repeat center center;
	}
	&__link-skype::before {
		background: url("@{bg-img}icon-skype.png") no-repeat center center;
	}
	&__link-tg::before {
		background: url("@{bg-img}icon-tg.png") no-repeat center center;
	}

}
.working-hours {
	display: block;
	margin-top: 20px;
	.ff-300();
	font-size: @font;
	color: @c-w;
}