.page-footer {
	display: flex;
	justify-content: center;
	padding-top: 40px;
	padding-bottom: 10px;
	background-color: #24241f;
	&__top,
	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: @c-w;
	}
	&__top {
		width: 100%;
		padding-bottom: 40px;
		border-bottom: 1px solid #50504c;
		@media(max-width: @phone) {
			justify-content: center;
		}
	}
	&__bottom {
		width: 100%;
		padding-top: 25px;
		padding-bottom: 25px;
		@media(max-width: @phone) {
			flex-wrap: wrap;
			justify-content: center;
		}
	}
}
.social-title {
	color: @c-w;
}
.social-list {
	display: flex;
	flex-wrap: wrap;
	margin-top: 15px;
	&__link {
		position: relative;
		width: 31px;
		height: 31px;
		margin-right: 10px;
		font-size: 0;
		border: 1px solid @c-w;
		border-radius: 3px;
	}
	&__link::after {
		content: '';
		position: absolute;
	}
	&__link:hover,
	&__link:focus {
		transform: scale(1.05);
		//border-color: @c-blue;
	}
	&__link-fb::after {
		top: 3px;
		left: 9px;
		width: 12px;
		height: 22px;
		background: url("@{bg-img}icon-fb.svg") no-repeat center center;
	}
	&__link-google::after {
		top: 2px;
		left: 3px;
		width: 28px;
		height: 25px;
		background: url("@{bg-img}icon-google.svg") no-repeat center center;
	}
	&__link-inst::after {
		top: 2px;
		left: 2px;
		width: 25px;
		height: 25px;
		background: url("@{bg-img}icon-inst.svg") no-repeat center center;
	}

}
.copirite {
	text-align: center;
}
.disigne-dev {
	display: flex;
	align-items: center;
	margin-left: 10px;
}