.our-clients {
	display: flex;
	justify-content: center;
	padding-bottom: 42px;
	padding-top: 10px;
	background-color: @c-bluel;
}
.slider-items {
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	width: 100%;
	overflow: hidden;
	img {
		width: auto;
	}
}
.slider {
	width: 100%;
	margin-top: 20px;

}
.slider-scrol {
	margin-top: 30px;
	padding-top: 20px;
	padding-bottom: 20px;
	cursor: pointer;
}
.scrolling-strip {
	position: relative;
	width: 100%;
	height: 5px;
	border-radius: 5px;
	background-color: @c-grey-l;
	&__toggle {
		position: absolute;
		top: -3px;
		left: 50px;
		width: 32px;
		height: 11px;
		font-size: 0;
		border-radius: 4px;
		background-color: @c-blue;
	}
	&__toggle::after {
		content: '';
		position: absolute;
		left: 12px;
		top: 3px;
		width: 7px;
		height: 5px;
		border-radius: 1px;
		background-color: @c-w;
	}
}