.main {
	text-align: center;
}
.hero {
	display: flex;
	justify-content: center;
	width: 100%;
	min-height: 90vh;
	margin-top: 50px;
	padding-bottom: 30px;
	@media(max-width: @tablet-p) {
		margin-top: 25px;
	}
}
.page-title{
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	color: #fff;
	.ff-500();
	&--big {
		font-size: @font-48;
		@media(max-width: @tablet-p) {
			font-size: @font-36;
			line-height: 1.2;
		}
	}
	&--small {
		font-size: @font-36;
		@media(max-width: @tablet-p) {
			font-size: @font-28;
			line-height: 1.4;
		}
	}
}
.promo {
	display: flex;
	width: 100%;
	margin-top: 40px;
	flex-direction: column;
	align-items: center;
	.ff-300();
	color: #ffffff;
	@media(max-width: @tablet-p) {
		margin-top: 25px;
	}
	&__slogan {
		font-size: @font-22;
		@media(max-width: @tablet-p) {
			font-size: 14px;
			line-height: 1.2;
		}
	}
	&__garant {
		margin-top: 20px;
		font-size: @font-14;
		@media(max-width: @tablet-p) {
			margin-top: 12px;
		}
	}
	&-form {
		display: flex;
		margin-top: 50px;
		flex-direction: column;
		align-items: center;
		@media(max-width: @tablet-p) {
			margin-top: 20px;
		}
		input {
			width: 290px;
			margin-bottom: 20px;
			margin-left: 5px;
			margin-right: 5px;
			padding: 15px 25px;
			border-radius: 25px;
			outline: none;
		}
	}
}
.input-wrap {
	z-index: 2;
}
.btn-order {
	position: relative;
	padding: 20px 50px;
	border-radius: 50px;
	background-color: @c-blue;
	font-size: @font;
	.ff-500();
	color: #ffffff;
	&::before,
	&::after {
		content:'';
		position: absolute;
		width: 345px;
		height: 78px;
		@media(max-width: 1000px) {
			display: none;
		}
	}
	&::before {
		left: 246px;
		top: -46px;
		border-bottom: 1px solid #8d8c83;
		border-top: 1px solid #8d8c83;
		border-right: 1px solid #8d8c83;
		z-index: 1;
	}
	&::after {
		right: 246px;
		top: -46px;
		border-bottom: 1px solid #8d8c83;
		border-top: 1px solid #8d8c83;
		border-left: 1px solid #8d8c83;
		z-index: 1;
	}
}
.scrol-btn {
	margin-top: 60px;
	margin-left: auto;
	margin-right: auto;
	background-color: transparent;
	transition: all 0.1s linear;
	&:hover {
		transform: translateY(5px);
		background-color: transparent;
	}
	@media(max-width: 1000px) {
		display: none;
	}
}
// Our Service

.our-service {
	display: flex;
	justify-content: center;
	width: 100%;
	padding-top: 30px;
	padding-bottom: 70px;
	background-color: #ffffff;
}
.cards {
	width: 100%;
	padding-top: 10px;
}
.card {
	display: flex;
	justify-content: space-between;
	align-items: center;
	text-align: left;
	&-title {
		.ff-300();
		font-size: @font-28;
		color: @c-blue;
	}
	&-discription {
		margin-top: 10px;
		.ff-100();
		font-size: @font-18;
		color: @c-grey-b;
	}
	&-btn {
		width: 180px;
		margin-top: 25px;
		padding: 16px;

	}
	&-text {
		max-width: 550px;
	}
	&-picture {
		max-width: 550px;
		max-height: 320px;
	}
}
// Calculator
.calculation-of-cost {
	display: flex;
	justify-content: center;
	min-height: 622px;
	padding-top: 25px;
	background: #534b41 url("@{bg-img}calculator-bgl.jpg") no-repeat center top;
	@media(max-width: @phone) {
		padding-top: 10px;
	}
	&__text {
		width: 100%;
	}
	&__discr {
		width: 100%;
		margin-top: 15px;
		.ff-300();
		font-size: @font-26;
		color: @c-w;
		@media(max-width: @phone) {
			font-size: @font;
		}
	}
}
.calculation-of-cost .block-title {
	@media(max-width: @phone) {
		font-size: 32px;
	}
}
.calculation-of-cost .block-title::after {
	background-color: @c-w;
}
.map {
	height: 600px;
	background: url('@{bg-img}map.jpg') no-repeat top left;
}