.our-team {
	display: flex;
	justify-content: center;
	padding-top: 10px;
	padding-bottom: 60px;
	background-color: @c-w;
}
.our-team-wrap {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding-top: 15px;
	padding-left: 65px;
	padding-right: 25px;
	@media(max-width: @tablet-p) {
		padding-left: 20px;
		padding-right: 0;
	}
	@media(max-width: @phone) {
		padding-left: 0;
	}
}
.card-worker {
	display: flex;
	flex-direction: column;
	width: 230px;
	margin-right: 45px;
	margin-top: 45px;
	@media(max-width: @tablet-p) {
		margin-right: 25px;
	}
	@media(max-width: @phone) {
		width: 100%;
		margin-right: 0;
	}
	&__photo {
		width: 225px;
		height: 225px;
		overflow: hidden;
		border-radius: 50%;
		@media(max-width: @tablet-p) {
			width: 175px;
			height: 175px;
			margin: 0 auto;
		}
	}
	&__name {
		margin-top: 10px;
		.ff-300();
		font-size: @font-24;
		color: @c-blue;
	}
	&__position {
		margin-top: 10px;
		.ff-300();
		font-size: @font;
		color: @c-grey-b;
	}
}
.quote {
	display: flex;
	position: relative;
	margin-top: 60px;
	flex-direction: column;
	align-items: center;
	&::after,
	&::before {
		content: "";
		position: absolute;
		width: 45px;
		height: 38px;
		@media(max-width: @tablet-p) {
			width: 32px;
			height: 25px;
		}
	}
	&::after {
		top:-7px;
		left: 0;
		transform: scale(1, -1);
		background: url("@{bg-img}icon-quotes.svg") no-repeat top center;
	}
	&::before {
		bottom:0;
		right: 0;
		background: url("@{bg-img}icon-quotes.svg") no-repeat top center;
		transform: rotate(180deg);
	}
	&__slogan,
	&__text,
	&____author-position {
		.ff-300();
		color: @c-grey-b;
	}
	&__slogan {
		max-width: 950px;
		margin-top: 15px;
		font-size: @font-32;
		@media(max-width: @phone) {
			font-size: 30px;
		}
	}
	&__text {
		margin-top: 15px;
		max-width: 950px;
		font-size: @font-18;
		@media(max-width: @phone) {
			font-size: @font-14;
		}
	}
	&__author {
		margin-top: 15px;
		.ff-500();
		font-size: @font-18;
		color: @c-grey-b;
	}
	&____author-position {
		margin-top: 15px;
		font-size: @font;
	}
}