.advantages {
	display: flex;
	justify-content: center;
	padding-top: 10px;
	padding-bottom: 40px;
	background-color: #f5f9fa;
	&__img-wrap {
		position: relative;
		width: 125px;
		height: 75px;
		border-bottom: 1px solid @c-blue;
		z-index: 10;
		img {
			width: auto;
		}
	}
}
.advantages-list {
	display: flex;
	padding-top: 10px;
	flex-wrap: wrap;
	justify-content: center; 
	&__item {
		position: relative;
		display: flex;
		margin-top: 30px;
		margin-right: 30px;
		flex-direction: column;
		align-items: center;
		width: 260px;
		min-height: 160px;
		@media(max-width: @tablet-p) {
			margin-top: 15px;
		}
	}
	&__item:nth-child(4n),
	&__item:last-child {
		margin-right: 0;
	}
}
.advantages__discr {
	.ff-500();
	font-size: @font-14;
	margin-top: 20px;
	color: #000000;
	&::after {
		position: absolute;
		content: "";
		display: block;
		width: 36px;
		height: 36px;
		top: 0;
		left: 138px;
		border-radius: 50%;
		background-color: #ade4f4;
		z-index: 0;
	}
}