.calc {
	display: flex;
	margin-top: 20px;
	color: @c-w;
	@media(max-width: 1000px) {
 		flex-wrap: wrap;
 		justify-content: center;
 		width: 100%;
 		margin-top: 0;
	}
	&-items {
		@media(max-width: @phone) {
			width: 100%;
		}
	}
}
.calc-item {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	width: 100%;
	max-width: 650px;
	height: 100%;
	max-height: 130px;
	padding-left: 20px;
	padding-right: 20px;
	margin-top: 10px;
	border: 1px solid @c-w;
	border-radius: 5px;
	@media(max-width: 1000px) {
		flex-direction: column;
		justify-content: flex-start;
		max-width: 210px;
		max-height: 215px;
		margin-top: 0;
	}
	@media(max-width: @phone) {
		flex-direction: row;
		max-width: 100%;
		padding-left: 5px;
		padding-right: 5px;
	}
	&__title {
		height: 25px;
		margin-top: 15px;
		.ff-400();
		font-size: @font-20;
		@media(max-width: @phone) {
			margin-top: 5px;
		}
	}
	&__scale {
		@media(max-width: 1000px) {
			display: none;
		}
	}
}
.scale {
	position: relative;
	width: 605px;
	height: 45px;
	margin-top: 5px;
	cursor: pointer;
}
.scale__strip {
	position: absolute;
	width: 585px;
	height: 9px;
	bottom: 5px;
	left: 2px;
	font-size: 0;
	background-color: rgba(255,255,255,.5);
}
.scale__point {
	position: absolute;
	display: block;
	width: 20px;
	height: 20px;
	.ff-300();
	font-size: 0;
	background-color: @c-w;
	border-radius: 50%;
}
.scale__point1 {
	bottom: 0;
	left: 0;
	&::after {
		content: '1м2';
		position: absolute;
		width: 75px;
    height: 20px;
    bottom: 30px;
    left: -18px;
		font-size: @font;
		color: @c-w;
	}
}
.scale__point2 {
	bottom: 0;
	left: 205px;
	&::after {
		content: '3500м2';
		position: absolute;
		width: 75px;
    height: 20px;
    bottom: 30px;
    left: -21px;
		font-size: @font;
		color: @c-w;
	}
}
.scale__point3 {
	bottom: 0;
	left: 410px;
	&::after {
		content: '7000м2';
		position: absolute;
		width: 75px;
    height: 20px;
    bottom: 30px;
    left: -24px;
		font-size: @font;
		color: @c-w;
	}
}
.scale__point4 {
	bottom: 0;
	left: 585px;
	&::after {
		content: '10000м2';
		position: absolute;
		width: 90px;
    height: 20px;
    bottom: 30px;
    left: -44px;
		font-size: @font;
		color: @c-w;
	}
}
.bar {
	position: relative;
	width: 25px;
	height: 25px;
	bottom: -23px;
	left: 0;
	font-size: 0;
	border: 10px solid @c-blue;
	border-radius: 50%;
}
.pacage-group {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	@media(max-width: 1000px) {
		flex-direction: column;
		justify-content: center;
	}
	@media(max-width: @phone) {
		flex-direction: row;
		justify-content: center;
	}

	&__item {
		.btn();
		width: 190px;
		padding-top: 10px;
		padding-bottom: 10px;
		border: 1px solid @c-w;
		border-radius: 5px;
		background-color: transparent;
		@media(max-width: 1000px) {
			margin-top: 10px;
		}
		@media(max-width: @phone) {
			max-width: 160px;
			margin-right: 5px;
			margin-top: 5px;
		}
		&:last-child {
			@media(max-width: @phone) {
				margin-right: 0;
			}
		}
	}
	&__item--active {
		background-color: darken(@c-blue, 20%);
		&:hover {
			background-color: darken(@c-blue, 20%);
		}
	}
}

//calc-form -----------------------------------------------------------
.calc-form {
	max-width: 350px;
	max-height: 360px;
	@media(max-width: 1000px) {
	 	width: 100%;
	 	padding-top: 20px;
	}
	@media(max-width: @phone) {
	 	width: 100%;
	 	padding-top: 10px;
	}
}
.calc-form__btn {
	padding: 15px;
	.ff-500();
	font-size: @font-14;
}
input[type="number"],
.calc-input {
	max-width: 310px;
	margin-top: 10px;
	text-align: center;
	.ff-400();
	font-size: @font-32;
	color: @c-w;
	border: none;
	background-color: transparent;
	outline: none;
	@media(max-width: @phone) {
		margin-top: 5px;
		font-size: @font-28;
	}
	&-label {
		display: block;
		margin-top: 10px;
		.ff-400();
		font-size: @font;
	}
}
/* .calc-form {
	margin-left: 100px;
} */
.calc-form__btn {
	margin-top: 30px;
	@media(max-width: @phone) {
		margin-top: 15px;
	}
}
.calc-phone {
	margin-top: 25px;
	.ff-300();
	font-size: @font-14;
	@media(max-width: @phone) {
		margin-top: 15px;
	}
}